@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/roboto-condensed-regular.woff2"), url("../fonts/roboto-condensed-regular.woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/roboto-condensed-700.woff2"), url("../fonts/roboto-condensed-700.woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "PT Mono";
  src: url("../fonts/pt-mono-regular.woff2"), url("../fonts/pt-mono-regular.woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap; }

html,
body {
  height: 100%;
  margin: 0; }

body {
  font-size: 24px;
  line-height: 33px;
  font-weight: normal;
  padding: 0;
  margin: 0;
  min-width: 320px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  box-sizing: border-box;
  background-color: #ffffff; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"],
input[type="radio"],
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.content {
  display: flex;
  flex-direction: column;
  position: relative;
  min-height: 100%; }

.content__footer {
  flex: 0 0 auto; }

.checkbox__label {
  font-size: 18px;
  line-height: 24px;
  font-weight: normal;
  padding-left: 25px;
  position: relative;
  cursor: pointer; }
  .checkbox__label input[type="checkbox"] + .checkbox__indicator {
    position: absolute;
    width: 16px;
    height: 16px;
    background: linear-gradient(360deg, #f3f3f3 0%, rgba(243, 243, 243, 0) 100%);
    border: 1px solid #d7d7d7;
    border-radius: 3px;
    top: 0;
    left: 0; }
  .checkbox__label input[type="checkbox"]:hover + .checkbox__indicator, .checkbox__label input[type="checkbox"]:focus + .checkbox__indicator {
    border: solid 1px rgba(215, 215, 215, 0.7); }
  .checkbox__label input[type="checkbox"]:active + .checkbox__indicator {
    opacity: 0.3; }
  .checkbox__label input[type="checkbox"]:checked + .checkbox__indicator::before {
    content: "";
    position: absolute;
    background-image: url("../img/tick.svg");
    background-repeat: no-repeat;
    width: 12px;
    height: 9px;
    top: 3px;
    left: 1px; }

.button {
  font-size: 14px;
  line-height: 16px;
  font-weight: 700;
  text-transform: uppercase;
  font: inherit;
  text-align: center;
  border: none; }
